import React, { Component } from 'react';
import Helmet from 'react-helmet';
import Header from './Header';
import Blogcontent from './Blogcontent';
import Search from './Search';
import Ad from './Ad';
import seologo from './../assets/images/trendingscan.png';
import logo from './../assets/images/logo.png';
import './../assets/css/Main.css';

export default class Homepage extends Component {
    constructor(props){
		super(props);
		this.state={
		  filter : {
			name:"country",
			value:"India"
		  }
		}
		this.changefilter=this.changefilter.bind(this);
	}
    
    changefilter(filterdata){
		switch(filterdata.name){
		  case "country":
			  this.setState({filter : filterdata})
			  break;
		  case "query":
			  break;
		  default:
			  break;
		}
    }
	detectmobile() {
        if(/(android|bb\d+|meego).+mobile|avantgo|bada\/|blackberry|blazer|compal|elaine|fennec|hiptop|iemobile|ip(hone|od)|ipad|iris|kindle|Android|Silk|lge |maemo|midp|mmp|netfront|opera m(ob|in)i|palm( os)?|phone|p(ixi|re)\/|plucker|pocket|psp|series(4|6)0|symbian|treo|up\.(browser|link)|vodafone|wap|windows (ce|phone)|xda|xiino/i.test(navigator.userAgent) 
    || /1207|6310|6590|3gso|4thp|50[1-6]i|770s|802s|a wa|abac|ac(er|oo|s\-)|ai(ko|rn)|al(av|ca|co)|amoi|an(ex|ny|yw)|aptu|ar(ch|go)|as(te|us)|attw|au(di|\-m|r |s )|avan|be(ck|ll|nq)|bi(lb|rd)|bl(ac|az)|br(e|v)w|bumb|bw\-(n|u)|c55\/|capi|ccwa|cdm\-|cell|chtm|cldc|cmd\-|co(mp|nd)|craw|da(it|ll|ng)|dbte|dc\-s|devi|dica|dmob|do(c|p)o|ds(12|\-d)|el(49|ai)|em(l2|ul)|er(ic|k0)|esl8|ez([4-7]0|os|wa|ze)|fetc|fly(\-|_)|g1 u|g560|gene|gf\-5|g\-mo|go(\.w|od)|gr(ad|un)|haie|hcit|hd\-(m|p|t)|hei\-|hi(pt|ta)|hp( i|ip)|hs\-c|ht(c(\-| |_|a|g|p|s|t)|tp)|hu(aw|tc)|i\-(20|go|ma)|i230|iac( |\-|\/)|ibro|idea|ig01|ikom|im1k|inno|ipaq|iris|ja(t|v)a|jbro|jemu|jigs|kddi|keji|kgt( |\/)|klon|kpt |kwc\-|kyo(c|k)|le(no|xi)|lg( g|\/(k|l|u)|50|54|\-[a-w])|libw|lynx|m1\-w|m3ga|m50\/|ma(te|ui|xo)|mc(01|21|ca)|m\-cr|me(rc|ri)|mi(o8|oa|ts)|mmef|mo(01|02|bi|de|do|t(\-| |o|v)|zz)|mt(50|p1|v )|mwbp|mywa|n10[0-2]|n20[2-3]|n30(0|2)|n50(0|2|5)|n7(0(0|1)|10)|ne((c|m)\-|on|tf|wf|wg|wt)|nok(6|i)|nzph|o2im|op(ti|wv)|oran|owg1|p800|pan(a|d|t)|pdxg|pg(13|\-([1-8]|c))|phil|pire|pl(ay|uc)|pn\-2|po(ck|rt|se)|prox|psio|pt\-g|qa\-a|qc(07|12|21|32|60|\-[2-7]|i\-)|qtek|r380|r600|raks|rim9|ro(ve|zo)|s55\/|sa(ge|ma|mm|ms|ny|va)|sc(01|h\-|oo|p\-)|sdk\/|se(c(\-|0|1)|47|mc|nd|ri)|sgh\-|shar|sie(\-|m)|sk\-0|sl(45|id)|sm(al|ar|b3|it|t5)|so(ft|ny)|sp(01|h\-|v\-|v )|sy(01|mb)|t2(18|50)|t6(00|10|18)|ta(gt|lk)|tcl\-|tdg\-|tel(i|m)|tim\-|t\-mo|to(pl|sh)|ts(70|m\-|m3|m5)|tx\-9|up(\.b|g1|si)|utst|v400|v750|veri|vi(rg|te)|vk(40|5[0-3]|\-v)|vm40|voda|vulc|vx(52|53|60|61|70|80|81|83|85|98)|w3c(\-| )|webc|whit|wi(g |nc|nw)|wmlb|wonu|x700|yas\-|your|zeto|zte\-/i.test(navigator.userAgent.substr(0,4))) { 
            return true;
        }else{
            return false;
        }
     }  

	getAddress (latitude, longitude) {
	fetch('https://maps.googleapis.com/maps/api/geocode/json?latlng=' + latitude + ',' + longitude + '&key=AIzaSyBn7F5tuFmuhWoC8ntngoGZ2RYjVG9vcWA')
	.then(response=> {return response.json()})
	.then(response=>{
			// console.log('User\'s Address Data is ', response);
			if(response.results && Object.keys(response.results).length){
				this.setState({filter:{name:"country",value:response.results[0].address_components[9].long_name}});
			}
		}).catch(err=>{
		    console.log('Request failed.  Returned status of',err);
	    })
	}
	componentWillMount(){
		// this.getmylocation();
		// this.ipLookUp();
	}
    render() {
		return (
            <div>
                <Helmet
					title="Home"
					meta={[
						{name:'fb:page_id',content:'189414828370961'},
						{property:'fb:app_id',content:'462468354180027'},
						{property:'og:url',content:'http://trendingscan.com/'},
						{property:'og:title',content:'Trendingscan - Top Trending Searches'},
						{property:'og:type',content:'article'},
						{property:'og:description',content:'Explore Top Trending Searches On Internet With Trendingscan'},
						{property:'og:image',content:seologo},
						{itemprop:'name',content:'Trendingscan - Top Trending Searches'},
						{itemprop:'description',content:'Explore Top Trending Searches On Internet With Trendingscan'}, 
						{itemprop:'image',content:seologo}
					]}
					link={[
						{rel:"alternate",hreflang:"x-default",href:"http://trendingscan.com/"},
						{rel:"canonical",href:"http://trendingscan.com/"}
					]}
				/>
				<div className="container-fluid">
                    <div className='row'>
                        <div className="sticky header">
                        <Header logo_url={logo} />
                        </div>
                    </div>
                    <div className='row contentroot'>
                        <div className="col-sm-3 col-xs-3 col-md-3 col-lg-3 hidden-xs body searchoptions" >
                            <div className="col-sm-12 col-xs-12 col-md-12 col-lg-12">
                                <Search filter={this.changefilter}/>
                                <div>
                                {/* <Ad position={"LEFT"} content={""}/> */}
                                </div>
                            </div>
                        </div>
                        <div className="col-sm-6 col-xs-6 col-md-6 col-lg-6 body">
                            <Blogcontent filter={this.state.filter} />
                        </div>
                        <div className="col-sm-3 col-xs-3 col-md-3 col-lg-3 hidden-xs body" >
                            <div className="sticky col-sm-3 col-xs-3 col-md-3 col-lg-3">
                                <div>
                                <Ad position={"RIGHT"} content={""}/>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
		);
	}
}